.product {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: white;
    z-index: 1;
}

.product > img {
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}

.product > button {
   background: linear-gradient(to bottom, #F6DC9C, #F0C24F);
   border: 1px solid;
   margin-top: 10px;
   border-color: #a88734 #9c7e31 #846a29; 
   color: #111
}

.product__price {
    margin-top: 5px;
}

.product__info {
    height: 100px;
    margin-bottom: 15px;
}

.product__rating {
    display: flex;
}

.product__star {
    width: 10px;
    height: 10px;
}